import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { CapituloNewDTO } from 'src/app/models/historia/capitulo-new.dto';
import { CapituloUpdateDTO } from 'src/app/models/capitulo/capitulo-update.dto';
import { CapituloNewUpdateDTO } from 'src/app/models/historia/capitulo-new-update.dto';

@Injectable({
  providedIn: 'root'
})
export class CapituloService {

  private staticID: string;

  public getStaticID(): string {
    return this.staticID;
  }

  public setStaticID(id: string): void {
    this.staticID = id;
  }

  constructor(private http: HttpClient, private storage: StorageService) { }

  findPageByCriador(page: string = '0', linesPerPage: string = '20'): Observable<any> {
    const orderBy = 'titulo';
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('orderBy', orderBy)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}capitulo/criador`,
      {
        params,
        observe: 'body',
      });
  }

  insert(dto: CapituloNewDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}capitulo`,
      dto);
  }

  update(dto: CapituloNewUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}capitulo`,
      dto);
  }

  findById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}capitulo/${id}`);
  }

  doCopy(idCapitulo: string): Observable<any> {
    const params = new HttpParams()
      .set('idUser', this.storage.getLocalUserId())
      .set('idCapitulo', idCapitulo);

    return this.http.get(`${API_CONFIG.baseUrl}capitulo/copiar`,
      {
        params,
        observe: 'body',
      });
  }
}
