import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';
import { FaccoesService } from 'src/app/services/criacoes/faccoes.service';

import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { CapituloService } from 'src/app/services/historia/capitulo.service';

import { EspecializacoesViewComponent } from '../../utils/especializacoes-view/especializacoes-view.component';
import { EspecializacoesComponent } from '../../utils/especializacoes/especializacoes.component';
import { VantagemViewComponent } from '../../utils/vantagem-view/vantagem-view.component';

@Component({
  selector: 'app-capitulo-detail',
  templateUrl: './capitulo-detail.component.html',
  styleUrls: ['./capitulo-detail.component.scss'],
})
export class CapituloDetailComponent implements OnInit {

  capitulo: any;
  missoes: any[] = [];
  loading = true;

  constructor(
    private modal: ModalController,
    private modalInterno: ModalController,
    private detalharService: DetalharService,
    private capituloService: CapituloService,
    private toast: MessageService,
  ) {

  }

  ngOnInit() {
    const id = this.detalharService.getId();

    this.capituloService.findById(id)
      .subscribe(res => {
        this.capitulo = res;
        this.missoes = res.missoes;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }


  close() {
    this.modal.dismiss(null, null, "capitulo-detail");
  }

  mudaAba(event) {
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  getRecompensas(recompensas: []) {
    const recompensa = recompensas.join(', ')
    return recompensa;
  }


}
